<script setup lang="ts">
import { ElSelect } from 'element-plus'

interface OptionItemType {
    label: string
    value: string
    disabled: boolean
}

defineProps({
    options: {
        type: Array as PropType<OptionItemType[]>,
        required: true,
    },
})

/**
 * 使用 onRemoteMethod 触发远程搜索
 */
const emit = defineEmits(['update:modelValue', 'remoteMethod'])

const value = defineModel({ required: true })

/** 是否正在中文合成拼音 */
const isComposition = ref(false)

function compositionstart() {
    isComposition.value = true
}

function compositionend() {
    isComposition.value = false
}

function remoteMethod(query: string) {
    if (!query)
        return
    if (isComposition.value)
        return

    emit('update:modelValue', query)
    emit('remoteMethod', query)
}
</script>

<template>
    <ElSelect v-model="value" v-bind="$attrs" :remote-method="remoteMethod" @compositionstart="compositionstart" @compositionend="compositionend">
        <ElOption v-for="item in options" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled" />
        <template #loading>
            <el-icon class="is-loading">
                <i class="i-uiw:loading" />
            </el-icon>
        </template>
    </ElSelect>
</template>

<style scoped lang="scss">

</style>
